@import url(https://fonts.googleapis.com/css?family=Rubik:300,400,500,700,900);
h1,
h2,
h3,
h4,
h5,
h6,
span,
div,
p {
    font-family: 'Rubik', sans-serif;
}

#calendar {
    display: block;
    margin: 0 auto;
    background-color: #fff;
    max-width: 300px;
}

.calendar-header {
    background-color: #0b3157;
    max-width: inherit;
    border-radius: 2px 2px 0 0;
}

.calendar-navigation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;
}

.calendar-navigation .nav-btn {
    border: none;
    color: #41b1b6;
    font-size: 9px;
    font-weight: 700;
    text-transform: uppercase;
    background-color: transparent;
    cursor: pointer;
    display: flex;
    outline: none;
}

.prev-btn {
    text-align: left;
}

.next-btn {
    text-align: right;
}

.calendar-navigation .current-month {
    font-size: 12px;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
    text-align: center;
}

.calendar-navigation .current-month:after {
    display: inline-block;
    margin-left: 5px;
    vertical-align: middle;
    content: ' ';
    background-image: url(data:image/svg+xml;utf8;base64,);
    background-size: 10px 10px;
    height: 10px;
    width: 10px;
    cursor: pointer;
    transition: -webkit-transform .1s ease-in-out;
    transition: transform .1s ease-in-out;
    transition: transform .1s ease-in-out, -webkit-transform .1s ease-in-out;
}

.calendar-navigation .current-month.active:after {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.days-of-week {
    display: flex;
    padding-bottom: 3px;
}

.days-of-week .day-of-week {
    margin: 0.25em;
    color: #70869c;
    font-size: 8px;
    flex-grow: 1;
    flex-basis: 0;
    text-align: center;
}

.week {
    display: flex;
}

.week .day {
    margin: 2px;
    position: relative;
    flex-grow: 1;
    flex-basis: 0;
    text-align: center;
    font-size: 10px;
    font-weight: 500;
    padding: 5px 0 0 0;
    line-height: 20px;
    cursor: pointer;
    transition: .3s all ease;
}

.week .day p {
    margin:0;
}

.month {
    flex-basis: 0;
    flex-grow: 1;
}

.selected {
    background-color: #0b3157;
    color: #fff !important;
    border-radius: 2px;
}

.current::after {
    position: absolute;
    font-size: 18px;
    content: '\2022';
    top: -5px;
    right: 38%;
    color: red;
}

.ordinary {
    color: #2e4e6f;
}

.weekend {
    color: #F06543;
}

.selectmode button {
    background-color: #0b3157;
    color: #70869c;
    border: 1px solid #70869c;
    border-radius: 10px;
    font-size: 11px;
    padding: 2px 10px;
    cursor: pointer;
    outline: none;
}

.selectmode button:first-child {
    margin-right: 20px;
}

.selectmode {
    display: flex;
    padding: 3px 0;
    justify-content: center;
}

.space{
    margin: 2px;
    flex-grow: 1;
    flex-basis: 0;
    padding: 5px 0;
    line-height: 20px;
    transition: .3s all ease;
}

.add-event-panel {
    background-color: #0b3157;
    padding-top: 8px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    border-radius: 20px 20px 0 0;
    border-color: #0b3157;
}

.add-event-panel button {
    display: block;
    background-color: #0b3157;
    color: #41b1b6;
    font-size: 10px;
    margin: 0 16px;
    padding: 5px 8px;
    border: 1px solid #41b1b6;
    border-radius: 50%;
    cursor: pointer;
    outline: none;
}

.event-header{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin: 10px;
}

.selected-date{
    font-weight: 600;
    padding: 0 6px;
    color: #0B3157;
    font-size: 12px;
    text-align: left;
}

.event-date {
    margin-left: 5px;
}

.event-date h2 {
    text-transform: uppercase;
    font-size: 10px;
    margin: 0 10px;
    color: #0B3157;
    opacity: 0.3;
}

.event {
    background-color: #45B4B7;
    border-radius: 2px;
    margin: 5px 5px;
    padding: 10px;
}

.event .event-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.event h5 {
    padding-left: 8px;
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 500;
    color: #FFFFFF;
    opacity: 0.5;
    margin: 0;  
}

.event .event-time {
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 500;
    color: #FFFFFF;
    opacity: 0.5;
}

.event .event-description {
    padding-left: 8px;
    color: #fff;
    font-size: 12px;
    font-weight: 500;
    margin: 5px 0;
}

.calendar-header .add-event-modal{
    position: absolute;
    width: 100%;
    height: 100%;
    max-width: inherit;
    background-color: #E8E9EB;
    z-index: 999;
    opacity: 1;
}

.calendar-header .add-event-header{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: nowrap;
    background-color: #0b3157;
    padding: 10px 10px;
    box-sizing: border-box;
}

.calendar-header .add-event-header button {
    font-size: 24px;
    color: #41b1b6;
    background-color: transparent;
    cursor: pointer;
    border: none;
}

.calendar-header .add-event-header h5 {
    font-size: 13px;
    padding-top: 5px;
    font-weight: 500;
    color: #fff;
    margin: 5px 0;
}

.add-container{
    max-width: inherit;
}

.event-marker {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    width: 55%;
    
}

.event-marker div{
    height: 3px;
    margin: 0 0 2px 0;  
}

.event-marker span {
    background-color: #41b1b6;
    height: 3px;
    border-radius: 3px;
    width: 100%;
    margin: 0 0.6px;
}

.add-event-input-block:hover input,
.add-event-input-block:hover h5{
    background-color: white;
}

.add-event-input-block input {
    border: none;
    width: 92%;
    font-size: 12px;
    font-family: 'Rubik', sans-serif;
    border-bottom: 1px solid #d6dadf;
    padding: 10px 12px;
    font-weight: 300;
    outline: none;
    color: #788CA0;
    background-color: #E8E9EB;
}

.add-event-input-block h5 {
    border-bottom: 1px solid #d6dadf;
    border: none;
    margin: 0;
    background-color: #E8E9EB;
    width: 92%;
    color: #788CA0;
    font-size: 11px;
    text-transform: uppercase;
    font-weight: 400;
    font-family: 'Rubik', sans-serif;
    padding: 10px 12px 0 12px;
}
